.sliderimg {
    position: relative;
    text-align: center;
    color: white;
}

.sliderimg > img {
    width: 100%;
    height: 500px;
    object-fit: cover
}

.sliderimg > p {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: rgba(0, 0, 0, .8);
    width: 100%;
    min-height: 5em;
    padding: 70px 0;
    font-size: 3em;
}
